img {
    width: 100%;
    height: auto;
}
.profileInfoContainer {
    display: flex;
}
.contacts {
    flex-grow: 2;
}
.profileInfo ul li{
list-style: none;
}

.addPhotoButtonContainer {
    position: absolute;
    top: 0px;
    width: 100%;
    background-color: rgba(180, 158, 240, 1);
    padding: 5px 0px 5px 0px;
    display: none;
}
.addPhotoButtonContainer input {
    color: white;
    margin-left: 5px;
}
.profilePictureContainer {
    position: relative;
    width: 200px;
    height: 200px;
    cursor: pointer;
    overflow: hidden;
}
.loadingPhotoContainer {
   position: absolute;
   top: 0px;
   width: 100%;
   height: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: rgba(194, 199, 237, 0.7);
   color: white;
   font-size: 18px;
}
.hideLoading {
    display: none;
}
.modal {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    display: none;
}
.modalContainer {
    position: relative;
    width: 500px;
    height: 500px;
    border-radius: 10px;
    background-color: white;
    overflow: hidden;
}
.modalContainer img {
    width: 100%;
}
.hideModalButton {
    position: absolute;
    width: auto;
    height: auto;
    top: 10px;
    right: 10px;
}
